import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import { BASE_URL,  ServiceURL} from './routes'
import 'rxjs/add/operator/map';


@Injectable()
export class RemoteServiceTreinamentoProvider {
	private headers;
	private httpOptions;
	private headersLocal;
	private httpOptionsLocal;
	private tokenHttpOptions;
	


	
  	constructor(public http: Http) {
			console.log('Hello RemoteServiceProvider Provider remote-service');
			this.headers = new Headers({
				'Content-Type': 'application/json'
			});
			this.httpOptions = new RequestOptions({
				headers: this.headers
			});

			this.headersLocal = new Headers({
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
			});
			this.httpOptionsLocal = new RequestOptions({
				headers: this.headersLocal
			});
  	}
	

	private configureTokenHeader(token){
		let tokenHeader = new Headers({
			'Accept': 'application/json;',
			'Authorization': 'Bearer ' + token,
			'Content-Type': 'application/json'
		});

		this.tokenHttpOptions = new RequestOptions({
			headers: tokenHeader
		});


	}
	private configureTokenHeaderForm(token){
		let tokenHeader = new Headers({
			'Authorization': 'Bearer ' + token
		});

		this.tokenHttpOptions = new RequestOptions({
			headers: tokenHeader
		});


	}



	all(token){

		this.configureTokenHeader(token);

		let res = new Promise((resolve, reject) => {
	
			this.http.get(ServiceURL.TREINAMENTO, this.tokenHttpOptions).subscribe((response) => {
				let data = response.json();
				console.log(data);
				if (data.error) {
					//Token Invlido
					resolve(false);
				}
				else if (data.success) {
					resolve(data.success);
				}

			}, error => {
				
				reject(error);
			});
		});
		return res;
	}

	add(token,dados){

		this.configureTokenHeader(token);

		let res = new Promise((resolve, reject) => {
	
			let body = JSON.stringify(dados);

			this.http.post(ServiceURL.TREINAMENTO, body,this.tokenHttpOptions).subscribe((response) => {
				let data = response.json();
				console.log(data);
				if (data.error) {
					//Token Invlido
					resolve(false);
				}
				else if (data.success) {
					resolve(data.success);
				}

			}, error => {
				
				reject(error);
			});
		});
		return res;
	}

	update(token,dados){

		this.configureTokenHeader(token);

		let res = new Promise((resolve, reject) => {
	
			let body = JSON.stringify(dados);

			this.http.put(ServiceURL.TREINAMENTO, body,this.tokenHttpOptions).subscribe((response) => {
				let data = response.json();
				console.log(data);
				if (data.error) {
					//Token Invlido
					resolve(false);
				}
				else if (data.success) {
					resolve(data.success);
				}

			}, error => {
				
				reject(error);
			});
		});
		return res;
	}


}
