import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import { BASE_URL,  ServiceURL} from './routes'
import 'rxjs/add/operator/map';


@Injectable()
export class RemoteServiceFuncionariosProvider {
	private headers;
	private httpOptions;
	private headersLocal;
	private httpOptionsLocal;
	private tokenHttpOptions;
	


	
  	constructor(public http: Http) {
			console.log('Hello RemoteServiceProvider Provider remote-service');
			this.headers = new Headers({
				'Content-Type': 'application/json'
			});
			this.httpOptions = new RequestOptions({
				headers: this.headers
			});

			this.headersLocal = new Headers({
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
			});
			this.httpOptionsLocal = new RequestOptions({
				headers: this.headersLocal
			});
  	}
	

	private configureTokenHeader(token){
		let tokenHeader = new Headers({
			'Accept': 'application/json;',
			'Authorization': 'Bearer ' + token,
			'Content-Type': 'application/json'
		});

		this.tokenHttpOptions = new RequestOptions({
			headers: tokenHeader
		});


	}
	private configureTokenHeaderForm(token){
		let tokenHeader = new Headers({
			'Authorization': 'Bearer ' + token
		});

		this.tokenHttpOptions = new RequestOptions({
			headers: tokenHeader
		});


	}



	all(token){

		this.configureTokenHeader(token);

		let res = new Promise((resolve, reject) => {
	
			this.http.get(ServiceURL.FUNCIONARIO, this.tokenHttpOptions).subscribe((response) => {
				let data = response.json();
				console.log(data);
				if (data.error) {
					//Token Invlido
					resolve(false);
				}
				else if (data.success) {
					resolve(data.success);
				}

			}, error => {
				
				reject(error);
			});
		});
		return res;
	}

	add(token,dados){

		
		this.configureTokenHeaderForm(token);

		let res = new Promise((resolve, reject) => {
	
			const formData: FormData = new FormData();
			formData.append('nome', dados.nome);
			formData.append('idUnidade', dados.idUnidade);
			formData.append('rg', dados.rg);
			formData.append('emissor', dados.emissor);
			formData.append('dataAdmissao', dados.dataAdmissao);
			formData.append('dataNascimento', dados.dataNascimento);
			formData.append('dataVencimentoAso', dados.dataVencimentoAso);
			formData.append('cnh', dados.cnh);
			formData.append('categoriaCnh', dados.categoriaCnh);
			formData.append('idTipoSaguinio', dados.idTipoSaguinio);
			formData.append('idCargo', dados.idCargo);
			formData.append('tercerizado', dados.tercerizado);
			formData.append('empresaTerceira', dados.empresaTerceira);
			formData.append('status', dados.status);
			formData.append('foto', dados.foto);

			this.http.post(ServiceURL.FUNCIONARIO, formData,this.tokenHttpOptions).subscribe((response) => {
				let data = response.json();
				console.log(data);
				if (data.error) {
					//Token Invlido
					resolve(false);
				}
				else if (data.success) {
					resolve(data.success);
				}

			}, error => {
				
				reject(error);
			});
		});
		return res;
	}

	update(token,dados){

		this.configureTokenHeader(token);

		let res = new Promise((resolve, reject) => {
	
			let body = JSON.stringify(dados);

			this.http.put(ServiceURL.FUNCIONARIO, body,this.tokenHttpOptions).subscribe((response) => {
				let data = response.json();
				console.log(data);
				if (data.error) {
					//Token Invlido
					resolve(false);
				}
				else if (data.success) {
					resolve(data.success);
				}

			}, error => {
				
				reject(error);
			});
		});
		return res;
	}

	byUnidade(idUnidade,token){

		this.configureTokenHeader(token);

		let res = new Promise((resolve, reject) => {
	
			this.http.get(`${ServiceURL.FUNCIONARIO}?idUnidade=${idUnidade}`, this.tokenHttpOptions).subscribe((response) => {
				let data = response.json();
				console.log(data);
				if (data.error) {
					//Token Invlido
					resolve(false);
				}
				else if (data.success) {
					resolve(data.success);
				}

			}, error => {
				
				reject(error);
			});
		});
		return res;
	}

}
