import { Injectable } from '@angular/core';
import {JwtTokenService} from '../local-storage/jwt-token'
import {NotificationeService} from '../notification/notification'
import { Router,ActivatedRoute } from '@angular/router';
import { formatDate } from "@angular/common";

declare var $: any;
@Injectable()
export class DefaultService {

  constructor(
    private jwtToken: JwtTokenService,
    private router: Router,
    private route: ActivatedRoute,
    private notifification: NotificationeService,
  ) { }


  error(error,title){
      console.log("ERROR: ",error);

    if(error.hasOwnProperty('status') || error.status == 401){
        this.notifification.show('top','right','danger',title,'No autorizado faa login novamente!');
        this.jwtToken.token = '';
        this.router.navigate(['/login'], { relativeTo: this.route.parent });
      }else{
        this.notifification.show('top','right','danger',title,error);
      }
    
    }

  async formatDDMMYYYY(myDate){

    const format = 'dd/MM/yyyy';
    // const myDate = '2019-06-29';
    const locale = 'en-US';
    return formatDate(myDate, format, locale);

  }

  async formatYYYYMMDD(myDate){

    const format = 'yyyy-MM-dd';
    // const myDate = '2019-06-29';
    const locale = 'en-US';
    return formatDate(myDate, format, locale);

  }


}
